<template>
  <div id="welcome">
    <el-card>
      <el-container>
        <el-header height="100px">
          <h1 style="text-align:center">!{{name}}</h1>
          <div class="box">
            <div class="left">
            <span>:</span> <span>{{uncheceked}}</span>
            </div>
            <div class="right">
              <span>:</span> <span>{{pass}}</span>
            </div>
          </div>
        </el-header>
        <el-container>
          <el-aside width="400px">
            <div id="pie" :style="{width: '400px', height: '400px'}"></div>
          </el-aside>
          <el-main>
            <a href="#">>></a>
            <div id="box">
              <ul id="animate"  :class="{ anim: animate == true }">
                  <li v-for='(item, index) in items' :key="index">{{item.importTime}}{{item.departmentName}}{{item.importerName}}{{item.systemName}}</li>
              </ul>
            </div>
          </el-main>
        </el-container>
      </el-container>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      name: '',
      uncheceked: '',
      pass: '',
      animate: false,
      items: []
    }
  },
  created () {
    // 
    this.$get('/api/v1/user/' + window.sessionStorage.getItem('userid'))
      .then(response => {
        this.name = response.data.data.name
      })
    this.drawPie()
    // 
    this.$get('/api/v1/sysInfo/carouselData')
      .then(response => {
        this.items = response.data.data
      })
    //  
    this.$get('/api/v1/sysInfo/countByAuthority')
      .then(response => {
        this.uncheceked = response.data.data[0]
        this.pass = response.data.data[1]
      })
    // 
    setInterval(this.scroll, 2000)
  },
  methods: {
    // 
    drawPie () {
      this.$get('/api/v1/sysInfo/groupGradeNum')
        .then(response => {
          const gradenum = response.data.data
          // chartscharts
          const myChart = this.$echarts.init(document.getElementById('pie'))
          // 
          myChart.setOption({
            title: {
              text: '',
              left: 'center' // 
            },
            tooltip: {
              trigger: 'item',
              formatter: '{b} : ({d}%)'
            },
            legend: {
              bottom: 10,
              left: 'center', // 
              data: ['', '', '']
            },
            series: [
              {
                type: 'pie',
                radius: '65%',
                center: ['50%', '50%'],
                selectedMode: 'single',
                data: [
                  { value: gradenum.grade2num, name: '' },
                  { value: gradenum.grade3num, name: '' },
                  { value: gradenum.grade4num, name: '' }
                ],
                emphasis: {
                  itemStyle: {
                    shadowBlur: 10,
                    shadowOffsetX: 0,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                  }
                }
              }
            ]
          })
        })
        .catch(err => err)
    },
    // 
    scroll () {
      this.animate = true
      setTimeout(() => {
        this.items.push(this.items[0])
        this.items.shift()
        this.animate = false
      }, 500)
    }
  }
}
</script>

<style lang="less" scoped>
.el-card {
  background-color: gainsboro;
}
.box {
  display: flex;
  width: 600px;
  margin: 0 auto;
}
.left{
  width: 300px;
  flex: 1;
}
// 
#box{
  width: 640px;
  overflow: hidden;
  padding-left: 10px;
}
.anim{
  transition: all 0.5s;
  margin-top: -30px;
}
#animate li{
  list-style: none;
  line-height: 30px;
  height: 30px;
}

</style>
